import React, { useState, useEffect } from 'react';

import moment from 'moment';
import 'moment/locale/ko';	//

function Time(props) {
  
  const [momentDate, setMomentDate] = useState(moment().format('YYYY.MM.DD HH:mm:ss'));
  
  console.log(" ..?") //   !   .

  useEffect(() => {
    let time = setInterval(() => {
      setMomentDate(moment().format('YYYY.MM.DD HH:mm:ss'));
    }, 1000);
    return () => {
  	    //    setinterval clearinterval
        clearInterval(time);
    };
  } );

  return (
    <div className="noCss"> 
        {momentDate}
    </div>
  );
}

export default Time;